<template>
  <div class="wrap-g "style="min-height: 100vh">
    <mt-header  :title="title" style="background: #fff;color: #1B1B1B;">
      <router-link to="/my" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <!--<mt-button icon="more" slot="right"></mt-button>-->
    </mt-header>
    <div class="list" style="border-top:1px solid #f2f2f2 ">
      <div class="item">
        历史总赏金：0.00元
      </div>
      <div class="item">
        可提现金额：<span style="color: #F19149">10元</span>
      </div>
    </div>

    <div class="list" style="margin-top: 15px">
      <div class="item">
        已提现金额 <span style="color: #F19149">10元</span>
        <div style="color: #BFBFBF">请到微信钱包查看具体收益</div>
      </div>

    </div>

    <div  class="msg"  style="margin: 25px">
      <div>使用说明</div>
      <div style="color: #F19149;margin: 15px 0">平台方、直播间和被打赏人怎么分配赏金？</div>
      <div>微信官方会扣除2%手续费，剩下的部分由直播间创建者和被打赏人按照上方设置的比例进行分成</div>
      <div  style="color: #F19149;margin: 15px 0">赏金如何提现？</div>
      <div>属于个人的赏金可以自助提现</div>
      <div>属于直播间的赏金，直播间创建者可在直播间设置页面提现到自己钱包</div>

    </div>

  </div>

</template>

<script>
  export default {
    data () {
      return {
        title:"收益"
      }
    },
    methods:{
    },
    mounted(){

    }
  }
</script>


<style lang="less" scoped>
  .msg div{
    font-family: PingFang SC;
    line-height: 25px;
  }
  .list{
    background: #fff;
    padding: 0 15px;
    .item{
      border-bottom: 1px solid #f2f2f2;
      line-height: 40px;
    }
  }

</style>



